import {InputField} from "./InputField";
import {Simulate} from "react-dom/test-utils";
import submit = Simulate.submit;
import {useState} from "react";
import {ProductData} from "../models";
import axios from "axios";
import {ErrorMessage} from "./ErrorMessage";


const productData : ProductData = {
    title: '',
    price: 13.5,
    description: 'lorem ipsum set',
    image: 'https://i.pravatar.cc',
    category: 'electronic',
    rating: {
        rate: 10,
        count: 10
    }
}

interface CreateProductProps {
    onCreate: (product: ProductData) => void;
}

export function CreateProduct(props: CreateProductProps) {
    const [value, setValue] = useState('')
    const [error, setError] = useState('')
    
    const submitHandler = async (event: React.FormEvent) => {
        event.preventDefault()

        setError('')
        if (value.trim().length === 0) {
            setError("Please enter a valid title");
            return;
        }

        productData.title = value;

        const response = await axios.post<ProductData>('https://fakestoreapi.com/products', productData)
        const data = response.data as ProductData;
        props.onCreate(data);
    }

    const changeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
        setValue(event.target.value)
    }

    return (
        <form onSubmit={submitHandler}>
            <input
                className="border p-4 mb-2 w-full outline-none"
                type="text"
                placeholder="Enter product name"
                value={value}
                onChange={changeHandler}
            />

            {error && <ErrorMessage message={error}></ErrorMessage>}
            <button
                className="w-full py-4 border-2 border-b-yellow-400 outline-none bg-yellow-400 rounded font-bold
                hover:bg-yellow-500/0 border-yellow-400 hover:text-yellow-400"
                type="submit"
            >Create</button>
        </form>
    )
}