import {ProductData} from "../models";
import {useState} from "react";

interface ProductProps {
    product: ProductData;
}


export function Product(props: ProductProps ) {
    const [details, setDetails] = useState(false)

    const btnBgClass = details ? 'bg-blue-400' : 'bg-yellow-400'

    return (
        <div
            className="border py-2 px-4 rounded flex flex-col items-center mb-2"
        >
            <img src={props.product.image} className="w-1/6" alt={props.product.title} />
            <p>{props.product.title}</p>
            <span className="font-bold">{props.product.price} $</span>

            <button
                onClick={() => {setDetails(prevState => !prevState)}}
                className={['rounded-2xl', 'py-2', 'px-4 border', btnBgClass].join(' ')}
            >
                { details ? 'Hide details' : 'Show details' }
            </button>

            {details && <div>
                <p>{props.product.description}</p>


                { ("rating" in props.product) &&
                    <p> Rate:
                        <span style={{
                            fontWeight: "bold"
                        }}
                        >
                            {props.product.rating.rate}
                        </span>
                    </p>
                }
            </div>}
        </div>
    )
}