import {useProducts} from "../hooks/products";
import React, {useContext} from "react";
import {ModalContext} from "../context/ModalContext";
import {ProductData} from "../models";
import {InputField} from "../components/InputField";
import {Loader} from "../components/Loader";
import {ErrorMessage} from "../components/ErrorMessage";
import {Product} from "../components/Product";
import {Modal} from "../components/Modal";
import {CreateProduct} from "../components/CreateProduct";


export function ProductPage() {
    const {products, error, addProduct} = useProducts();
    const {modalVisible, open, close} = useContext(ModalContext);


    const createHandler = (product: ProductData) => {
        close()
        addProduct(product)
    }

    return (
        <div className="container mx-auto max-w-2xl p-5">

            <InputField></InputField>

            {!products && <Loader></Loader>}

            {error && <ErrorMessage message={error}></ErrorMessage>}

            {products && products.map((product: ProductData) => (
                <Product key={product.id} product={product} />
            ))}

            {modalVisible &&
                <Modal title="Create new product" onCLose={close}>
                    <CreateProduct onCreate={createHandler}/>
                </Modal>
            }

            <button
                className="fixed bottom-5 right-5 rounded-full bg-red-600 text-white px-4 py-2"
                onClick={open}
            >+</button>
        </div>
    );
}