import {useEffect, useState} from "react";
import {ProductData} from "../models";
import axios from "axios";

export function useProducts() {
    const [products, setProducts] = useState<ProductData[]>(); // Initialize with `null`
    const [error, setError] = useState<string | null>(null); // Error handling state

    function addProduct(product: ProductData) {
        // @ts-ignore
        setProducts(prevState => [...prevState, product]);
    }

    async function fetchProducts() {
        try {
            console.log("Fetching products..."); // Debugging log to check API call
            const response = await axios.get<ProductData[]>("https://fakestoreapi.com/products?limit=5");
            console.log("Fetched data:", response.data); // Debugging log to check received data
            setProducts(response.data); // Set the fetched data
        } catch (err) {
            console.error("Error fetching products:", err); // Debugging log for errors
            setError("Failed to fetch products."); // Set an error message if the request fails
        }
    }

    useEffect(() => {
        fetchProducts(); // Fetch products when component mounts
    }, []);

    return { products, error, addProduct };
}